import React, { useState, useEffect } from "react";
import { useSelector } from 'react-redux'

import {
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow,
} from "@coreui/react";
import { useRouteMatch } from "react-router-dom";
import API from "src/api";
import { history } from "src/history";

const ChapterList = (props) => {
  const serverLink = useSelector(state => state.imageServer)

  const fields = ["preFix", "title", "image", "introduction", "view", "edit", "delete"];
  const [chapterData, setChapterData] = useState([{}]);
  const [isLoading, setIsLoading] = useState(false);
  let { url } = useRouteMatch();

  useEffect(() => {
    getData();
  }, []);

  async function getData() {
    try {
      setIsLoading(true);
      var response = await API.get("/chapter/list");
      setChapterData(response.data.data);
      setIsLoading(false);
    } catch (error) {
    }
  }

  const deleteChapter = async (id) => {
    try {
      // setIsLoading(true);
      var response = await API.delete("/chapter/" + id);
      if (response.status === 201) {
        var data = chapterData.filter((a) => {
          return a._id !== id;
        });
        setChapterData(data);
      }
      // setIsLoading(false);
    } catch (error) {
    }
  };

  return (
    <>
      <div className="">
        <CRow className="p-2 ml-auto"></CRow>
        <CRow>
          <CCol xs="12">
            <CCard>
              <CCardHeader>
                <span className="h4">Chapter List</span>
                <CButton
                  className="btn btn-primary float-right"
                  onClick={() => {
                    history.push("/chapter/add");
                  }}
                >
                  Add
                </CButton>
              </CCardHeader>
              <CCardBody>
                {isLoading ? (
                  <div>Loading...</div>
                ) : (
                  <CDataTable
                    items={chapterData}
                    fields={fields}
                    itemsPerPage={20}
                    loading={isLoading}
                    pagination
                    scopedSlots={{
                      image: (item) => (
                        <td>
                          <img
                            src={serverLink + item.image}
                            alt={item.title}
                            height="70"
                            width="70"
                          />
                        </td>
                      ),
                      edit: (item) => (
                        <td>
                          <CButton
                            className="btn btn-success"
                            onClick={() => {
                              history.push(url + "/edit/" + item._id);
                            }}
                          >
                            Edit
                          </CButton>
                        </td>
                      ),
                      view: (item) => (
                        <td>
                          <CButton
                            className="btn btn-info"
                            onClick={() =>
                              history.push(url + "/" + item._id + "/topics/")
                            }
                          >
                            View
                          </CButton>
                        </td>
                      ),
                      delete: (item) => (
                        <td>
                          <CButton
                            className="btn btn-danger"
                            onClick={(e) => {
                              deleteChapter(item._id);
                            }}
                          >
                            Delete
                          </CButton>
                        </td>
                      ),
                    }}
                  />
                )}
              </CCardBody>
            </CCard>
          </CCol>
        </CRow>
      </div>
    </>
  );
};

export default ChapterList;
